.item-info-container {
  margin-bottom: 20px;
  cursor: pointer;
  .img {
    height: 150px;
    width: 100%;
    object-fit: cover;
  }
  .box-content {
    margin-left: 38px;
    h1 {
      font-size: 18px;
      line-height: 22px;
      color: #000000;
      font-weight: 600;
      padding-bottom: 16px;
    }
  }
  .promotion_name {
    font-size: 18px;
    font-family: 'Google Sans', sans-serif;
    font-weight: 700;
  }
  .promotion_description {
    margin-top: 16px;
    font-size: 18px;
    font-family: 'Google Sans', sans-serif;
    font-weight: 400;
    p {
      font-size: 18px;
      line-height: 25px;
      font-weight: 400;
      font-family: 'Google Sans', sans-serif !important;
    }
    span {
      font-size: 19px;
      line-height: 25px;
      font-weight: 400;
      font-family: 'Google Sans', sans-serif !important;
    }
  }
  .line {
    margin-top: 20px;

    width: 100%;
    height: 4px;
    align-self: center;
    background-color: var(--line-pink);
  }
}
