.promotion-container {
  display: flex;
  flex-direction: column;
  background: var(--page-bg);
  .promotion_layout {
    display: flex;
    flex-direction: column;
    align-items: center;
  }
  .promotion_content {
    width: 85%;
  }
  .content {
    width: 80%;
    margin: 0 auto;
  }

  .img-banner {
    width: 100%;
    max-height: 450px;
  }
  .img-line {
    margin-bottom: 40px;
  }
  .box-router {
    height: 37px;
    display: flex;
    align-items: center;
  }
  .custom-button {
    background-color: var(--primary-pink);
    box-shadow: 1px 5px 5px rgba(254, 84, 102, 0.15);
    border-radius: 20px;
    width: 263px;
    height: 44px;
    color: white;
    font-size: 18px;
    line-height: 22px;
    align-self: center;
    margin: 40px 0px;
  }
  .box-button {
    display: flex;
    flex-direction: column;
  }

  @media screen and (max-width: 600px) {
    .promotion_content {
      width: 95%;
    }
  }
}
